entation/IRouter';\nimport { IUser } from '../../../../../../common/qCloudTemp/runtimeDtoObjects';\nimport CustomSelect from \"../../components/customSelect.vue\";\nimport { IDialog } from '../../../../domain/presentation/IDialog';\n\ninterface IOptions { name: string, id: string };\n\nconst workflow = window.workflow;\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst route = useRoute();\n\nconst wfFrameDiv = ref<HTMLDivElement>(null as any);\nconst userOptions = ref<Array<IOptions>>([]);\nconst usernameInput = ref<string>(\"\");\nconst defUserObj = ref<IOptions>();\nconst listUsers = ref<Array<IUser>>([]);\n\n\nonBeforeMount(() => {\n    const user = workflow.getUser();\n    usernameInput.value = user!.id;\n    defUserObj.value = { name: user!.name, id: user!.id };\n});\nonMounted( async ()=> {\n    openApplication();\n    listUsers.value = await workflow.listUsers();\n    convertUserOptions();            \n});\n\nconst selectedUserRoles = computed(() => listUsers.value.find((user) => user.id == usernameInput.value)?.roles || [])\nconst selectedUserSwimlane = computed(() => listUsers.value.find((user) => user.id == usernameInput.value)?.swimlane || [])\nconst actionButtons = [{buttonID: \"addNewUser\",buttonText: \"Add New\", buttonClass: \"new-user-button\", onClick: () => addNewUser()}]\nasync function openApplication() {\n    await workflow.startWorkflowView!(wfFrameDiv.value);\n}\nfunction convertUserOptions() {\n    let options: Array<IOptions> = [];\n    listUsers.value.forEach((item: IUser) => {\n        options.push({ name: item.name, id: item.id });\n    })\n    userOptions.value = options;\n}\nfunction selectUserName(value: string) {\n    usernameInput.value = value;\n    const userInfo = listUsers.value.find((user)=> user.id == value) as IUser;\n    workflow.setUser(userInfo!);\n    workflow.setIsNewProcess(false);\n    openApplication();\n}\nfunction exitPreview() {\n    window.close();\n}\nfunction addNewUser() {\n    executor.execute(async () => {\n        dialog.showDialog(defineAsyncComponent(() => import(\"../../components/dialogs/create/createNewWorkflowUser.vue\")), { closable: true, width: '604px', height: '400px', title: 'Create New User', closeOnOutClick: true });\n    }, { loading: true });\n}\n\n\n</script>\n<template>\n    <header class=\"header\">\n        <div class=\"d-flex\">\n            <CustomSelect class=\"username\" :defaultForObject=\"defUserObj\" :options=\"userOptions\" :multiple=\"false\" :extraButton=\"true\" :buttons=\"actionButtons\" @input=\"selectUserName\" />\n            <span v-for=\"role in selectedUserRoles\" :key=\"role\" class=\"chip\">{{role}}</span>\n            <span v-for=\"lane in selectedUserSwimlane\" :key=\"lane\" class=\"chip\">{{lane}}</span>\n        </div>\n        <p class=\"m-0\">Process Instance ID and other information</p>\n        <button class=\"exit-preview\" @click=\"exitPreview\">EXIT PREVIEW</button>\n    </header>\n    <div ref=\"wfFrameDiv\">\n        \n    </div>\n</template>\n<style scoped>\n@import url(\"../../../../styles/IDE3.css\");\n\n.header {\n    width: 100%;\n    height: 64px;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 14px 16px;    \n    background: white;\n    position: relative;\n    top: 0;\n    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;\n    z-index: 1;\n}\n.username {\n    width: 200px;\n    font-size: 15px;\n    height: 40px;\n    line-height: 40px;\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    padding: 0px 12px;\n    margin-right: 12px;\n}\n.header .chip {\n    background: #F5F5F5;\n    padding: 7px 10px;\n    border-radius: 4px;\n    margin-right: 8px;\n}\n.exit-preview {\n    height: 40px;\n    font-weight: 500;\n    font-size: 14px;\n    color: #151233;\n    background: #FFFFFF;\n    padding: 8px 16px;\n    border-radius: 4px;\n    border: 1px solid #151233;\n}\n.exit-preview:hover {\n    background: #151233;\n    color: #FFFFFF;\n}\n</style>\n<style>\n.username .items {\n    color: #212121;\n    border-radius: 8px;\n    border: 1px solid #E3E3E3;\n    overflow: hidden;\n    background-color: #FFFFFF;\n    box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n    margin-top: 8px;\n}\n\n.username .items .option {\n    color: #212121;\n    padding-left: 12px;\n    line-height: 32px;\n}\n\n.username .items .option:hover {\n    background-color: #EEEEEE;\n}\n\n.username i {\n    font-size: 16px;\n    color: #BCBCBC;\n}\n\n.username .button-div {\n    padding: 0 !important;\n    margin: 0 !important;\n}\n\n\n</style>\n\n"],"sourceRoot":""}]);
